@import "../../global.scss";

.portfolio{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1{
        font-size: 50px;
        @include mobile{
            font-size: 20px;
        }
    }

    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;

        @include mobile{
            margin: 10px 0px;
            flex-wrap: wrap;
            justify-content: center;
        }
    }

    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        @include mobile{
            width: 100%;
        }
    }

    .item{
        width: 300px;
        height: 200px;
        border-radius: 20px;
        border: 1px solid lightgrey;
        margin: 30px 60px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        position: relative;
        overflow: hidden;
        transition: all .5s ease;
        cursor: pointer;

        @include mobile{
            width: 130px;
            height: 100px;
            margin: 20px 30px;
        }

        h3{
            position: absolute;
            font-size: 20px;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            z-index: 1;
        }

        &:hover{ // On hovering from mouse it will create some effects.
            background-color: $mainColor;
            img{
                opacity: 0.2;
                z-index: 0;
            }
        }
    }
}
